import React from "react";
import visualVideo from '../videos/visual.mp4'; // MP4  

export default function Visual() {
    return (
        <div className="relative h-96 lg:h-screen">
            <div className="absolute lg:h-screen h-96 flex flex-col justify-center items-center w-full z-10 p-8 text-center">
                <p className="font-bold text-white text-3xl lg:text-8xl">Quick Survey<br />
    </p>
<p className="font-bold text-white text-sm pt-2 lg:text-2xl lg:pt-8">         .<br />
       .</p>
            </div>
        <video 
            className="absolute top-0 left-0 w-full h-full object-cover"
            autoPlay 
            muted 
            loop
        >
            <source src={visualVideo} type="video/mp4" />
            Your browser does not support the video tag.
        </video>
    </div>
    );
}
