
import Servicedep from "./servicedep";
import gsap from 'gsap';
import { ScrollTrigger } from 'gsap/ScrollTrigger';
import { useRef, useEffect } from "react";


gsap.registerPlugin(ScrollTrigger);


export default function SectionService01() {
  const t2aRef = useRef(null);
  const t2bRef = useRef(null);
  const t2cRef = useRef(null);


  useEffect(() => {
    const animateElements = () => {
      gsap.timeline({
        scrollTrigger: {
          trigger: t2aRef.current,
          start: 'top center',
          end: 'bottom center',
          scrub: true,
          toggleActions: 'play none none none',
          // markers: true,
        }
      })
  
      .fromTo(t2bRef.current, 
        { scale: 0, opacity: 0 }, 
        { scale: 1, opacity: 1, duration: 2, ease: 'power2.inOut' } //    ease  
      )
      .fromTo(t2cRef.current, 
        { scale: 0.5, opacity: 0 },
        { scale: 1, opacity: 1, duration: 2, ease: 'power2.inOut' } //    ease  
      );
  


    };
  
    animateElements();
  
    return () => {
      ScrollTrigger.getAll().forEach(trigger => trigger.kill());
    };
  }, []);
  

    return (




<div className="lg:h-screen service-section relative w-full" style={{background: 'rgba(249, 238, 234, 0.40)'}}>
<Servicedep />
  
  <div className='flex flex-col-reverse lg:flex-row items-center lg:justify-between '>

    <div className='flex flex-col w-full p-10 justify-center items-center text-center lg:text-left lg:items-start lg:p-0 lg:pl-60 lg:w-2/4'>
      <p className='text-4xl pb-6 lg:text-7xl font-bold lg:pb-10 w-full'> <br /><span className='text-c2'>  </span> </p>
      <div className='text-lg text-gray-800 pb-6 lg:pb-10 brnone' style={{ whiteSpace: "pre-line" }}>
         92    .<br />

, ,       , <br />
        .<br />
         .</div>
      <button className='rounded-full bg-c2 text-white text-lg w-3/4 lg:w-1/5 p-4'> </button>
    </div>
    
    <div className='w-full h-96 lg:h-screen lg:w-2/4 service-img lg:rounded-tl-[400px]'></div>

  </div>

</div>

    );
}
